.tb_ht_post_container{
    display: flex;
    flex-wrap: wrap;
    width: 100%;
}

.tb_ht_post_slider{
    width: 100%;
}

.tb_ht_post_wrapper{
    width: 100%;
    padding: 7.5px;
}

.tb_ht_post_in{
    display: block;
    position: relative;
    overflow: hidden;
    cursor: pointer;
    background-color: rgba(0, 0, 0, 0.02);
    border-radius: var(--tb-border-radius);
}

.tb_ht_post_cta {
    position: absolute;
    bottom: 15px;
    width: 100%;
    left: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 10px;
}

.tb_ht_post_cta_btn {
    font-family: var(--tb-font);
    padding: var(--tb-cta-padding);
    color: var(--tb-cta-color);
    background-color:  var(--tb-cta-bg-color);
    border-radius: var(--tb-cta-border-radius);
    font-weight: var(--tb-cta-font-weight);
    border-color: var(--tb-cta-border-color);
    transition: var(--tb-cta-transition);
    min-height: var(--tb-cta-height);
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 14px;
    line-height: normal;
}

.tb_ht_post_cta_btn:hover {
    color: var(--tb-cta-color-hover);
    background-color: var(--tb-cta-bg-color-hover);
    border-color: var(--tb-cta-border-color-hover);
}

.tb_ht_post_radius{
    border-radius: 50%;
}

.tb_ht_post_media_wrapp{
    position: relative;
}

.tb_ht_post_details, .tb_ht_post_overlay {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    padding: 10px;
    opacity: 0;
    z-index: 1;
    visibility: hidden;
    width: calc(100% + 1px);
    height: calc(100% + 1px);
}


.tb_ht_post_overlay-light{
    background-color: rgba(255, 255, 255, .3);
    background-image: linear-gradient(135deg, rgba(255, 255, 255, 0) 0%, rgba(255, 255, 255, .4) 100%);
}

.tb_ht_post_overlay-dark{
    background-color: rgba(0, 0, 0, .2);
    background-image: linear-gradient(135deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, .4) 100%);
}

.tb_ht_post_details {
    z-index: 4;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}

@media only screen and (min-width: 991px)  {
    .tb_ht_post_in:hover .tb_ht_post_details,
    .tb_ht_post_in:hover .tb_ht_post_overlay {
        opacity: 1;
        visibility: visible;
    }

    .tb_ht_post_in:hover .tb_ht_post_overlay.tb_ht_color_overlay {
        opacity: .8;
    }

    .tb_ht_post_in:hover .tb_ht_image {
        -webkit-transform: scale(1.07);
        transform: scale(1.07);
    }

    .tb_ht_post_in:hover .tb_ht_post_details{
        transform: translateY(10px);
        animation: TbSlideUpAnimation_ .65s ease forwards;
    }

    @-webkit-keyframes TbSlideUpAnimation_ {
        100% {
            transform: translateY(0);
        }
        100% {
            transform: translateY(0);
        }
    }

    @keyframes TbSlideUpAnimation_ {
        0% {
            transform: translateY(10px);
        }
        100% {
            transform: translateY(0);
        }
    }
}
.tb_ht_contant_wrapper {
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    margin: 0 auto;
    height: 100%;
    display: flex;
    align-items: center;
    padding: 15px;
}

.tb_ht_image_wrap_{
    padding-bottom: 100%;
    position: relative;
}
.tb_ht_image{
    height: 100% !important;
    width: 100% !important;
    position: absolute;
    left: 0;
    top: 0;
    z-index: 1;
    overflow: hidden;
    display: block;
    object-fit: cover;
    object-position: center;
    transition: all 1s ease-in-out;
}
.tb_ht_image_wrap_, .tb_ht_video_wrap_{
    padding-bottom: 100%;
    position: relative;
    background-color: rgba(0,0,0,.03);
}
.tb_ht_image, .tb_ht_video{
    height: 100% !important;
    width: 100% !important;
    position: absolute;
    left: 0;
    top: 0;
    margin: 0;
    z-index: 1;
    overflow: hidden;
    display: block;
    object-fit: cover;
    object-position: center;
    background-color: #000;
}

.tb_ht_video video{
    height: 100%;
    width: 100%;
    object-fit: cover;
    object-position: center;
}

.tb_ht_video iframe{
    pointer-events: none;
}
.tb_ht_social_ {
    display: flex;
    width: 100%;
    text-align: center;
    align-items: center;
    justify-content: center;
}

.tb_ht_social_ico_ {
    font-size: 22px;
    margin: 10px 0;
    color: var(--tb-icon-color);
}


.tb_ht_video_ico, .tb_ht_audio_ico {
    position: absolute;
    top: 15px;
    bottom: auto;
    left: auto;
    right: 15px;
    margin: auto 0;
    z-index: 2;
    color: #fff;
    font-size: 18px;
    opacity: 1;
    filter: drop-shadow(0px 0px 2px rgba(0,0,0,.5));
}

.tb_ht_audio_ico{
    font-size: 35px;
}

.tb_ht_multi_images_ico {
    position: absolute;
    right: 12px;
    top: 12px;
    color: #fff;
    font-size: 17px;
    z-index: 2;
    filter: drop-shadow(2px 2px 2px rgba(0,0,0,.2));
}

.tb_ht_multi_images_ico_bottom {
    position: absolute;
    right: 12px;
    bottom: 12px;
    color: #fff;
    font-size: 17px;
    z-index: 2;
    filter: drop-shadow(2px 2px 2px rgba(0,0,0,.2));
}

.tb_ht_post_animate .tb_ht_multi_images_ico_bottom{
    left: 12px;
    top: 12px;
    right: auto;
    bottom: auto;
}

.tb_ht_youtube_ico {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    margin: 0 auto;
    text-align: center;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 34px;
}

.tb_ht_arrow {
    position: absolute;
    top: calc(50% - 30px);
    color: #000;
    background-color: #fff;
    border-radius: 35px;
    height: 46px;
    width: 46px;
    display: flex;
    text-align: center;
    align-items: center;
    justify-content: center;
    font-size: 16px;
    z-index: 9;
    cursor: pointer;
    -webkit-transform: translateY(0) !important;
    transform: translateY(0) !important;
}

.tb_ht_arrow:hover{
    opacity: 1;
}

.tb_ht_arrow_left__{
    left: 10px;
    right: auto;
    box-shadow: 3px 0px 3px rgba(0, 0, 0, 0.15);
}

.tb_ht_arrow_right__{
    right: 10px;
    left: auto;
    box-shadow: -3px 0px 3px rgba(0, 0, 0, 0.15);
}


@media screen and (max-width: 991px) {
.tb_ht_arrow {
    top: calc(50% - 20px);
    height: 40px;
    width: 40px;
    font-size: 16px;
}    
}